.uri-rss-reader-feed {
  margin: 2rem 0;
}

.uri-rss-reader-item {
  display: block;
  column-gap: 1rem;
  align-items: center;
  max-width: 40rem;
  position: relative;
}

.uri-rss-reader-item:not(:last-child) {
  padding-bottom: 1rem;
  margin-bottom: 1.5rem;
  border-bottom: 1px solid #ddd;
}

.uri-rss-reader-item:after {
  content: "";
  display: block;
  clear: both;
}

.uri-rss-reader-image {
  padding-bottom: .5rem;
}

.uri-rss-reader-image img {
  display: block;
  width: 100px;
  height: 100px;
  float: left;
  border-radius: .2rem;
  margin-bottom: .5rem;
  margin-right: 2rem;
}

.no-thumbnail {
  background: url('https://www.uri.edu/wp-content/uploads/home/2025/01/no-img-th.jpg') no-repeat;
  float: left;
  background-size: 100px;
  border-radius: .2rem;
  margin-bottom: .5rem;
  margin-right: 2rem;
  width: 100px;
  height: 100px;
}

.uri-rss-reader-title-link a {
  display: block;
  font-size: 1rem;
  font-weight: 600;
  color: #000;
  font-family: "lato", "hind", Arial;
  line-height: 1.5rem;
  margin-bottom: .8rem;
}

.uri-rss-reader-title-link a:hover {
  color: #005eff;
  transition: color .1s ease-in-out;
}

.description p {
  font-family: "charter", "georgia", serif;
  font-size: 1rem;
  color: #555;
  line-height: 1.5rem;
  margin-bottom: .4rem;
}

.date p {
  font-family: "hind", Arial;
  font-size: .85rem;
  color: #555;
  text-transform: uppercase;
  letter-spacing: .5px;
  margin-bottom: .4rem;
}


@media screen and (min-width: 800px) {
  .uri-rss-reader-title-link a {
    font-size: 1.12rem;
    line-height: 1.65rem;
  }
}

@media screen and (max-width: 400px) {
  .uri-rss-reader-title-link a {
    font-size: .8rem;
    line-height: 1rem;
  }

  .description p {
    font-size: .8rem;
    line-height: 1.1rem;
  }
}